@import url(https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap);
@font-face {
  font-family: 'Dico';
  src: local('Dico'), url(/static/media/Dico.7acfb9b5.ttf) format('truetype')
}

:root {
  background-color: black;
  width: 100vw;
  font-family: 'Dico', sans-serif;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#pixel-bonfire {
  width: 200px;
  height: auto;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.textbox {
  color: white;
  max-width: 60%;
  line-height: 1.6;
  background-color: rgba(0, 0, 0, 0.5);
}
.title {
  color: rgb(165, 136, 97);
}
.subtitle {
  color: rgb(165, 136, 97);
  font-size: 1.3em;
}
